.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.server {
  background-color: yellow
}

table tr.spacer td {
  padding-top:2em;
}

.score {
  float: left;
  width:20%;
  outline: black 1px solid
}

.scorebox {
  width:90%;
  margin: 0 5% 0 5%;
  display: inline-block;
}

.court{
  width:90%;
  margin: 0 5% 0 5%;
  background-color: lightgreen;
  outline: black 1px solid;
  margin-bottom: 3em;
}

.side {
  width:100%;
  float: left;
  outline: black 1px solid;
}

.sideTitle {
  width:100%;
  outline: black 1px solid;
  font-weight: bold;
  height: 3em;
  vertical-align: middle;
  line-height: 3em;
  float:left;
  background-color:darkgreen;
  font-size: large;
  color: white;
}

.player {
  width:50%;
  float: left;
  height: 5em;
  font-size: x-large;
  vertical-align: middle;
  line-height: 5em;
  outline: black 1px solid;
  background-color: lightgreen
}

.server {
  background-color: yellow;
}

.scorebox div {
  padding: .5em 0 .5em 0
}

form {
  padding-top: 30px;
}

.net {
  background-color: white;
  line-height: 1.5em;
  padding:.25em;
  width: 100%;
}

.bench {
  width: 100%;
  float: left;
  margin: 2em 0 1em 0;
}

.benchTitle {
  font-weight: bold
}

.benchName {
  font-size: larger
}

.button {
  margin-top: 2em;
}

.link {
  margin-top: 2em;
}

table {
  width:100%;
  table-layout: fixed;
}

.winner {
  background-color: lightgreen
}

.notPlayed {
  background-color: lightgray
}

.gameWinner {
  background-color: goldenrod
}

.player-grid {
  display: grid;
  grid-template-columns: repeat(1, auto);
  width: 60%;
  margin: 0 20% 0 20%
}

.playerRow {
  display:grid;
  grid-template-columns: repeat(2, auto);
  margin-top: 1em;
}

.playerRow:first-child {
  margin-top: 2em
}

.playerRow:last-child {
  margin-bottom: 2em
}

spaceBelow {
  margin-bottom: 3em;
}



@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



